
import { defineComponent, PropType, ref, watch, computed } from 'vue'
import { IFormItem } from '../types'

export default defineComponent({
  name: 'fromView',
  props: {
    modelValue: {
      type: Object,
      required: true
    },
    formItems: {
      type: Array as PropType<IFormItem[]>,
      default: () => []
    },
    labelWidth: {
      type: String,
      default: '100px'
    },
    itemStyle: {
      type: Object,
      default: () => ({ padding: '10px 30px' })
    },
    colLayout: {
      type: Object,
      default: () => ({
        xl: 6, // 屏幕大于1920px 显示4个
        lg: 8, //
        md: 12, //
        sm: 24, //
        xs: 24
      })
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    // 浅拷贝 父组件传过来的值 在组件修改的是 浅拷贝的值 并不会违法单项数据流
    // 但是在点击重置的时候 修改的并不是浅拷贝的这个地址 所以在点击重置的时候要修改value属性的某一个属性值 并不是修改value
    // const formData = ref({ ...props.modelValue })

    // // 监听 formData 数据变化 发送事件 传给父组件
    // watch(
    //   formData,
    //   (newValue) => {
    //     emit('update:modelValue', newValue)
    //     console.log(formData.value)
    //   },
    //   //{deep: true} 深度监听
    //   { deep: true }
    // )

    const handelValueChange = (value: any, field: string) => {
      emit('update:modelValue', { ...props.modelValue, [field]: value })
    }

    return {
      // formData
      handelValueChange
    }
  }
})
